<template>
  <el-dropdown size='mini' style="float: right;">
    <el-link :underline="false" icon="el-icon-more"></el-link>
    <el-dropdown-menu slot="dropdown">
      <!-- <el-dropdown-item>
                  <el-button type="text"  size="mini" @click="download(scope.$index, scope.row)"></el-button>
                </el-dropdown-item> -->
      <el-dropdown-item>
        <el-button type="text" v-if="check(scope.row.isDir,scope.row.selfName,0)" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button type="text" v-if="check(scope.row.isDir,scope.row.selfName,1)" size="mini" @click="DLNA(scope.row.fid)"></el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,2)" type="text" @click="FileVersion( scope.row)"></el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,3)" type="text" @click="share(scope.row.fid)"></el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,4)" @click="rename(scope.row)" type="text"></el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,5)" type="text"></el-button>
      </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
  import {
    byteToSize,
    checkType
  } from '../../static/Utils.js'

  export default {
    data() {
      return {

      }
    },
    methods: {
      check(type,name,index){
        var video=[1,1,1,0,1,1];
        var folder=[1,0,0,1,0,0];
        var music=[1,1,1,0,1,1];
        var Other=[1,0,1,0,1,1];
        if(type==1){
          return folder[index]==1;
        }
        switch(checkType(name)){
          case "video":return video[index]==1;break;
        }
        return Other[index]==1;
      },
      rename(row){
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({
          value
        }) => {
          let form = new FormData();
          form.append("targetName", value);
          this.$axios.put('/api/disk/Rename/'+row.fid, form).then(responese => {
            if (responese.data.code === 20000) {
              this.reload();
            }
          });
        });
      },

      share(id) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({
          value
        }) => {
          let form = new FormData();
          form.append("fid", id);
          form.append("email", value);
          this.$axios.put('/api/disk/folder', form).then(responese => {
            if (responese.data.code === 20000) {
              this.reload();
            }
          });
        });
      },

      FileVersion(row) {
        this.$axios.post('/api/v1/FilesVersion/insertFilesVersionControl', {
          fid: row.fid + ''
        }).then(responese => {
          if (responese.data.code == 20000) {
            this.$notify({
              title: '',
              message: '!',
              type: 'success'
            });
          }
        });
      },

      DLNA(fid) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({
          value
        }) => {
          this.$axios.get('/api/Files/Share/DLNA', {
            params: {
              fid: fid,
              device: encodeURIComponent(value)
            }
          }).then(responese => {
            console.log(responese);
          })
        });
      },


      handleDelete(index, row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/disk/delete', {
            fid: row.fid
          }).then(responese => {
            if (responese.data.code == 20000) {
              this.$notify({
                title: '',
                message: '!',
                type: 'success'
              });
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
    },
    mounted() {
    },
    props: ['scope'],
  }
</script>

<style>
</style>



